#profile{
    margin: 0px;
    margin-top: 5rem;
    background-color: #CFC8B9;
    min-height: 55rem; 
    font-family: 'Nunito', sans-serif;
    color: black;
}

#profile_icon{
    width: 5rem;
}

#profile_img{
    width: 30rem;
    margin-bottom: 1rem;
    border: 0.3rem solid black;
    border-radius: 5rem;
}

#description{
    text-align: justify;
    font-size: 3.5rem;
    border: 0.3rem solid black;
    padding: 0.3rem;

    border-right-color: #CFC8B9;
    border-left-color: #CFC8B9;
}

ul{
    font-size: 2.5rem;
}

li{
    font-size: 2rem;
}

#curriculum_btn{
    margin: 2.5rem;
    margin-left: 8rem;
    border: 0.3rem solid black;
    background-color: #CFC8B9;
    color: black;
    -webkit-transition: 0.5s;
    transition: 0.5s;
}

#curriculum_btn:hover{
    background-color: black;
    color: white;
}
#skills{
    margin: 0px;
    min-height: 55rem;
    background-color: #ADA89C;
    font-family: 'Nunito', sans-serif;
    color: black;
}

#wrench_icon{
    width: 5rem;
}

ul{

}

li.skills_topic{
    display:inline;
    padding: 0.3rem;
    border: 0.2rem solid black;
    border-radius: 1rem;
    margin-right: 1rem;
    -webkit-transition: 0.5s;
    transition: 0.5s;   
}

li.skills_topic:hover{
    background-color: black;
    color: white;
    border:0.2rem solid black;
}
#blog{
    margin: 0px;

    background-color: #ACB1B1;
    min-height: 65rem; 
    font-family: 'Nunito', sans-serif;
    color: black;
}

#blog_icon{
    width: 5rem;
}

h2{
    text-align: center;
}
.post_img{
    width: 25rem;
    height: 25rem;
}

.post_desc{
    font-size: 2.5rem;
}

.post_container{
    display: flex;
    flex-direction: column; 
    text-align: center;
    border: 3px solid black;
    background-color: black;
    color: white;
    padding: 2rem;
    align-items: center;
    margin-bottom: 2.5rem;
}

.post_btn{    
    margin: 1rem;
    border: 0.3rem solid black;
    background-color: #ACB1B1;
    color: black;
    -webkit-transition: 0.5s;
    transition: 0.5s;
}

.post_btn:hover{
    background-color: black;
    color: white;
}
#portfolio{
    margin: 0px;
    background-color: #535C56;
    min-height: 55rem; 
    font-family: 'Nunito', sans-serif;
    color: white;
}

#book_icon{
    width: 5rem;
}

h2#portfolio_desc{
    text-align: center;
}

.port_btn{    
    margin: 1rem;
    border: 0.3rem solid black;
    background-color: white;
    color: black;
    -webkit-transition: 0.5s;
    transition: 0.5s;
}

.port_btn:hover{
    background-color: black;
    color: white;
}

.port_img{
    width: 20rem;
    height: 20rem;
}
#about{
    margin: 0px;
    background-color: #4E697F;
    min-height: 55rem; 
    font-family: 'Nunito', sans-serif;
    color: white;
}

#about_icon{
    width: 5rem;
}

.details_container{
    display: flex;
    flex-direction: column; 
    text-align: center;
    border: 3px solid white;
    border-radius: 2rem;
    color: white;
    padding: 2rem;
    align-items: center;
    margin-bottom: 5rem;
}
#contact{
    margin: 0px;
    margin-top: -20px;
    background-color: #0F2530;
    min-height: 55rem; 
    font-family: 'Nunito', sans-serif;
    color: white;
}

#phone_icon{
    width: 4.5rem;
}

#form{
    display: flex;
    flex-direction: column; 
    text-align: center;
    align-items: center;
    flex-shrink: 1;
}

::-webkit-input-placeholder{
    color: black;
}

::-ms-input-placeholder{
    color: black;
}

::placeholder{
    color: black;
}

#submit_btn{
    width: 30rem;
    border: 0.3rem solid white;
    background-color: white;
    color: black;
    margin: 0.5rem;
    -webkit-transition: 0.5s;
    transition: 0.5s;
    margin-bottom: 8rem;
}

#submit_btn:hover{
    background-color: #0F2530;
    color: white;
}

input{
    color: black;
    margin: 0.5rem;
    width: 30em;
    height: 4.5rem;
}

textarea{
    width: 30em;
    color: black;
    margin: 0.5rem;
    height: 10rem;
}

#github_icon{
    width: 13rem;
    margin: -2.5rem;
}

#facebook_icon{
    width: 8rem;
}

#git_col{
    text-align: center;

}

#fb_col{
    text-align: center;
}

#social_row{
    margin-top: 5rem;
    margin-bottom: 4rem;
}
#footer{
    margin: 0px;
    background-color: #000000;
    min-height: 10rem; 
    font-family: 'Nunito', sans-serif;
    color: white;
    text-align: center;
    margin-top: 3.5rem;
}
